#roomDiv{
    width: 25%;
    height: 100vh;
    float: left;
    text-align: center;
    background-color: rgb(247, 247, 247);
}

#roomHeader{
    width: 100%;
    height: 10vh;
    position: relative;
}

#roomName{
    margin: 30px auto;
    width: 65%;
    height: 35px;
    border: none;
    border-radius: 8px;
    background-color: rgb(235, 235, 235);
    text-indent: 10px;
}

#roomBtn{
    margin-left: 10px;
    width: 12%;
    height: 35px;
    border: none;
    border-radius: 5px;
    background-color: rgb(0, 165, 0);
    font-size: 15px;
    font-weight: bold;
    color: rgb(255, 255, 255);
    transition: all 0.2s;
}

#roomBtn:active{
    background-color: rgb(255, 255, 255);
    color: rgb(0, 165, 0);
    border: 2px solid rgb(0, 165, 0);
}

#roomList{
    width: 100%;
    height: 80vh;
    position: relative;
}

.roomBlock{
    width: 100%;
    height: 6vh;
    clear: both;
    transition: all 0.2s;
}

.roomBlock div{
    float: left;
}

.roomIcon, .chatIcon{
    margin: 10px 10px 0px;
    width: 10%;
    text-align: center;
}
  
.roomIcon img, .chatIcon img{
    width: 100%;
}

.roomName{
    margin: 18px 0px 0px;
    width: 60%;
    height: 30px;
}

#createBtn{
    width: 70%;
    height: 5vh;
    border: none;
    border-radius: 10px;
    background-color: rgb(48,48,48);
    font-size: 20px;
    font-weight: bold;
    color: rgb(255, 255, 255);
    transition: all 0.2s;
}

#createBtn:active{
    color: rgb(48, 48, 48);
    border: 1px solid rgb(48, 48, 48);
    background-color: rgb(255,255,255);
}

#mainDiv{
    width: 75%;
    height: 100vh;
    position: relative;
    float: left;
}

#mainDiv div{
    float: left;
}

#chatList{
    width: 95%;
    height: 100vh;
    position: relative;
    overflow-y: auto;
}

.chatBlock{
    margin: 20px 4% 0px;
    width: 90%;
    height: auto;
    padding: 10px;
    background-color: rgb(235, 253, 235);
    border-radius: 20px;
}

.chatContent{
    width: 85%;
}

.chatContent pre{
    white-space: normal;
}

.chatContent img{
    width: 80%;
}



#chatInsert{
    bottom: 0;
    position: fixed;
    width: 71%;
    height: 10vh;
    background-color: rgb(255, 255, 255);
    z-index: 1;
}

#chatText{
    margin: 30px 2% 0px 5%;
    width: 80%;
    height: 4vh;
    display: block;
    float: left;
    border: none;
    background-color: rgb(235, 235, 235);
    border-radius: 5px;
    text-indent: 15px;
    font-size: 18px;
}

#chatSend{
    margin: 30px 0 0px;
    width: 7%;
    height: 4vh;
    border: none;
    background-color: rgb(48, 48, 48);
    border-radius: 5px;
    font-size: 20px;
    font-weight: bold;
    color: rgb(255, 255, 255);
    transition: all 0.2s;
}

#chatSend:active{
    background-color: rgb(255,255,255);
    border: 1px solid rgb(48, 48, 48);
    color: rgb(48, 48, 48);
}


#uploadPopup{
    margin: 20% 10%;
    width: 80%;
    height: 30vh;
    border-radius: 25px;
    background-color: rgb(255, 255, 255);
    position: absolute;
    z-index: 1;
    box-shadow: 0px 0px 10px rgb(163, 163, 163);
}

#fileType{
    margin: 30px auto;
    width: 70%;
    height: 8%;
    display: block;
    border-radius: 5px;
    border: none;
    background-color: rgb(235, 235, 235);
}

#fileRadio{
    margin: 20px auto;
    width: 70%;
    height: 20%;
    text-align: center;
}

#file{
    margin: 0px auto 30px;
    width: 70%;
    display: block;
}

#uploadBtn{
    margin: 40px auto 0px;
    display: block;
    width: 150px;
    height: 40px;
    border: none;
    border-radius: 5px;
    background-color: rgb(48, 48, 48);
    color: rgb(255, 255, 255);
    transition: all 0.2s;
}

#uploadBtn:active{
    border: 1px solid rgb(48, 48, 48);
    background-color: rgb(255, 255, 255);
    color: rgb(48, 48, 48);
}

#cancleBtn{
    margin: 20px auto 0px;
    display: block;
    width: 150px;
    height: 40px;
    border: none;
    border-radius: 5px;
    background-color: rgb(226, 0, 0);
    color: rgb(255, 255, 255);
    transition: all 0.2s;
}

#cancleBtn:active{
    border: 1px solid rgb(48, 48, 48);
    background-color: rgb(255, 255, 255);
    color: rgb(48, 48, 48);
}


#chatRemocon{
    width: 5%;
    height: 100vh;
    background-color: rgb(235, 235, 235);
}

.remoconIcon{
    margin: 15% 10% 0px;
    width: 80%;
}

.remoconIcon img{
    width: 100%;
}
